import {
  CREATE_ENGAGEMENT_REQUEST,
  CREATE_ENGAGEMENT_SUCCESS,
  CREATE_ENGAGEMENT_FALUIRE,
  GET_BUSINESS_SEARCH_REQUEST,
  GET_BUSINESS_SEARCH_SUCCESS,
  GET_BUSINESS_SEARCH_FALUIRE,
  GET_EXPERT_SEARCH_REQUEST,
  GET_EXPERT_SEARCH_SUCCESS,
  GET_EXPERT_SEARCH_FALUIRE,
  GET_ENGAGEMENT_OPPORTUNITY,
  GET_ENGAGEMENT_OPPORTUNITY_SUCCESS,
  GET_ENGAGEMENT_OPPORTUNITY_FALUIRE,
  GET_ENGAGEMENT_HISTORY_REQUEST,
  GET_ENGAGEMENT_HISTORY_SUCCESS,
  GET_ENGAGEMENT_HISTORY_FALUIRE
} from "./types"

const initialState = {
  requesting: false,
  postSuccess: false,
  error: false,
  expertSearch: false,
  businessSearch: false,
  opportunity: false,
  engagementHistory: false
}

export default (state = initialState, action) => {
  switch (action.type) {
    case CREATE_ENGAGEMENT_REQUEST:
      return {
        ...state,
        requesting: true
      }

    case CREATE_ENGAGEMENT_SUCCESS:
      return {
        ...state,
        error: false,
        requesting: false,
        postSuccess: action.data
      }

    case CREATE_ENGAGEMENT_FALUIRE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    case GET_BUSINESS_SEARCH_REQUEST:
      return {
        ...state,
        requesting: true
      }

    case GET_BUSINESS_SEARCH_SUCCESS:
      return {
        ...state,
        error: false,
        requesting: false,
        businessSearch: action.data
      }

    case GET_BUSINESS_SEARCH_FALUIRE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    case GET_EXPERT_SEARCH_REQUEST:
      return {
        ...state,
        requesting: true
      }

    case GET_EXPERT_SEARCH_SUCCESS:
      return {
        ...state,
        error: false,
        requesting: false,
        expertSearch: action.data
      }

    case GET_EXPERT_SEARCH_FALUIRE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    case GET_ENGAGEMENT_OPPORTUNITY:
      return {
        ...state,
        requesting: true
      }

    case GET_ENGAGEMENT_OPPORTUNITY_SUCCESS:
      return {
        ...state,
        error: false,
        requesting: false,
        opportunity: action.data
      }

    case GET_ENGAGEMENT_OPPORTUNITY_FALUIRE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    case GET_ENGAGEMENT_HISTORY_REQUEST:
      return {
        ...state,
        requesting: true
      }

    case GET_ENGAGEMENT_HISTORY_SUCCESS:
      return {
        ...state,
        error: false,
        requesting: false,
        engagementHistory: action.data
      }

    case GET_ENGAGEMENT_HISTORY_FALUIRE:
      return {
        ...state,
        requesting: false,
        error: action.data
      }

    default:
      return state
  }
}
