import React, { useState, useEffect } from "react"
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Row,
  Col,
  Table,
  Spinner
} from "reactstrap"
import PaginationPack from "components/PaginationPack"
import { connect } from "react-redux"
import Images from "utils/Images"

import { getEngagementHistroryRequest } from "../redux/actions"

function EngagementsHistory(props) {
  const { engagementHistory, requesting } = props

  let PageSize = 10

  useEffect(() => {
    props.getEngagementHistroryRequest(1)
  }, [])

  const handlePageChange = page => {
    props.getEngagementHistroryRequest(page)
  }

  return (
    <>
      <div className="content">
        <Row>
          <Col md="12">
            <Card className="card-plain">
              <CardHeader>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    marginBottom: "20px"
                  }}
                >
                  <img
                    onClick={() => props.history.goBack()}
                    style={{
                      height: "40px",
                      width: "40px",
                      marginRight: "20px",
                      cursor: "pointer"
                    }}
                    src={Images.back_button}
                  />
                  <CardTitle
                    tag="h4"
                    style={{
                      fontFamily: "Libre Caslon Text",
                      fontStyle: "normal",
                      fontWeight: "700",
                      fontSize: "22px",
                      lineHeight: "27px",
                      color: "#CACACA"
                    }}
                  >
                    Engagements History
                  </CardTitle>
                </div>
              </CardHeader>
              <CardBody>
                <Table
                  responsive
                  style={{
                    border: "3px solid transparent"
                  }}
                >
                  {/* <thead> */}
                  <tr>
                    <th
                      style={{
                        color: "#808080",
                        padding: "25px"
                      }}
                    >
                      Experts
                    </th>
                    <th style={{ color: "#808080" }}>Client</th>
                    <th
                      className="text-center"
                      style={{
                        color: "#808080"
                      }}
                    >
                      Description
                    </th>
                    <th
                      className="text-center"
                      style={{
                        color: "#808080"
                      }}
                    >
                      Status
                    </th>

                    <th
                      className="text-right"
                      style={{
                        color: "#808080"
                      }}
                    >
                      Expert Rate
                    </th>
                    <th
                      className="text-right"
                      style={{
                        color: "#808080"
                      }}
                    >
                      Business Rate
                    </th>
                    <th
                      className="text-right"
                      style={{
                        color: "#808080"
                      }}
                    >
                      Rate
                    </th>
                    <th
                      className="text-right"
                      style={{
                        color: "#808080"
                      }}
                    >
                      StartDate
                    </th>
                    {/* 
                    <th
                      className="text-right"
                      style={{
                        color: "#808080",
                        paddingRight: "74px"
                      }}
                    >
                      Actions
                    </th> */}
                  </tr>
                  {/* </thead> */}

                  {requesting ? (
                    <div
                      style={{
                        position: "absolute",
                        top: "90%",
                        // transform: "translate(-50%, -50%)",
                        left: "50%"
                      }}
                    >
                      <Spinner
                        as="span"
                        animation="border"
                        size="lg"
                        style={{}}
                        role="status"
                        aria-hidden="true"
                      />
                    </div>
                  ) : engagementHistory &&
                    engagementHistory?.results.length < 0 ? (
                    <div
                      style={{
                        position: "absolute",
                        top: 120,
                        transform: "translate(-50%, -50%)",
                        left: "50%"
                      }}
                    >
                      <p>No Record Found.</p>
                    </div>
                  ) : (
                    <tbody>
                      {engagementHistory?.results &&
                        engagementHistory?.results.map(item => (
                          <>
                            <tr
                              style={{
                                // border: "3px solid",
                                // borderRadius: "50px !mportant",
                                backgroundColor: "white",
                                marginLeft: "25px",
                                border: "3px solid transparent"
                              }}
                            >
                              <td style={{ paddingLeft: "25px" }}>
                                {item.expert_name}
                              </td>

                              <td>{item.business_name}</td>
                              <td className="text-center">
                                {item.description}
                              </td>
                              <td className="text-center">
                                {item.Status ? "InActive" : "Active"}
                              </td>
                              <td className="text-right">
                                ${item.expert_rate}/hr
                              </td>
                              <td className="text-right">
                                ${item.business_rate}/hr
                              </td>
                              <td className="text-right">
                                ${item.rate_per_bill}/hr
                              </td>
                              <td className="text-right">
                                {item.agreement_start_date}
                              </td>
                            </tr>
                            <tr style={{ height: "12px" }}></tr>
                          </>
                        ))}
                    </tbody>
                  )}
                </Table>
                <div className="pt-4 d-flex justify-content-center">
                  {engagementHistory &&
                  engagementHistory?.results.length > 0 ? (
                    requesting ? null : (
                      <div className="pt-4 d-flex justify-content-center">
                        <PaginationPack
                          onChange={handlePageChange}
                          itemsCountPerPage={engagementHistory?.page_size}
                          totalItemsCount={engagementHistory?.total_objects}
                          activePage={engagementHistory?.current_page_number}
                        />
                      </div>
                    )
                  ) : null}
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  )
}

const mapStateToProps = state => ({
  engagementHistory: state.Engagements.engagementHistory,
  requesting: state.Engagements.requesting
})

const mapDispatchToProps = dispatch => ({
  getEngagementHistroryRequest: data =>
    dispatch(getEngagementHistroryRequest(data))
})
export default connect(mapStateToProps, mapDispatchToProps)(EngagementsHistory)
