import { all, call, put, takeLatest } from "redux-saga/effects"
import toast from "react-hot-toast"
// config
import { BASE_URL } from "config/app"

// utils
import XHR from "utils/XHR"

// types
import {
  CREATE_ENGAGEMENT_REQUEST,
  GET_BUSINESS_SEARCH_REQUEST,
  GET_EXPERT_SEARCH_REQUEST,
  GET_ENGAGEMENT_OPPORTUNITY,
  GET_ENGAGEMENT_HISTORY_REQUEST
} from "./types"

// actions
import {
  postEngagementSuccess,
  postEngagementFaluire,
  getBusinessDataSuccess,
  getBusinessDataFaluire,
  getExpertDataSuccess,
  getExpertDataFaluire,
  getEngagementOpportunitySuccess,
  getEngagementOpportunityFaluire,
  getEngagementHistrorySuccess,
  getEngagementHistroryFaluire
} from "./actions"
import { get } from "jquery"

async function getBusinessAccountsAPI(data) {
  // console.log('data', data);
  const accessToken = await sessionStorage.getItem("authToken")
  const URL = `${BASE_URL}/api/v1/siteadmin/admin_business/?search=${data}`
  const options = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: `Token ${accessToken}`
    },
    method: "GET"
  }

  return XHR(URL, options)
}

function* getBusinessAccounts({ data }) {
  try {
    const response = yield call(getBusinessAccountsAPI, data)
    // console.log("getBusinessAccounts Success..........", response)
    yield put(getBusinessDataSuccess(response?.data?.results))
  } catch (e) {
    const { response } = e
    // console.log("getBusinessAccounts failure..........", response)
    yield put(getBusinessDataFaluire(e.response))
  }
}

