async function createEngagementAPI(data) {
  const accessToken = await sessionStorage.getItem("authToken")
  const URL = `${BASE_URL}/api/v1/siteadmin/new_engagement/`
  const options = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: `Token ${accessToken}`
    },
    method: "POST",
    data
  }

  return XHR(URL, options)
}

function* createEngagement({ data }) {
  try {
    const response = yield call(createEngagementAPI, data)
    // console.log("createEngagement Success..........", response)
    toast.success("Successfully Created")
    yield put(postEngagementSuccess(response?.data))
  } catch (e) {
    const { response } = e
    // console.log("createEngagement failure..........", response)
    yield put(postEngagementFaluire(response?.data))
    toast.error("Failure")
  }
}

