import { all, call, put, takeLatest } from "redux-saga/effects"
import { push } from "connected-react-router"
// import AsyncStorage from '@react-native-community/async-storage';

// import { showMessage } from 'react-native-flash-message';
// import toast from "react-hot-toast"

import toast from "react-hot-toast"
// config
import { BASE_URL } from "../../../../../config/app"

// utils
import XHR from "../../../../../utils/XHR"

// types
import { GET_REFER_REQUEST, REFER_REQUEST } from "./types"

// actions
import { getReferSuccess, referSuccess, referFaluire } from "./actions"

async function getReferAPI(data) {
  // console.log("data>>>>>>", data)
  const accessToken = await sessionStorage.getItem("authToken")
  const URL = `${BASE_URL}/api/v1/dashboard/refer/?page=${data}`
  const options = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: `Token ${accessToken}`
    },
    method: "GET"
  }
  return XHR(URL, options)
}
function* getRefer({ data }) {
  try {
    const response = yield call(getReferAPI, data)
    // console.log('getRefer success.................', response);
    toast.success(` Get Refer Successfully`)
    yield put(getReferSuccess(response.data))
  } catch (e) {
    const { response } = e
    // console.log('getRefer failure.................', response);
    // toast.error(`${response}`)
    yield put(getReferSuccess(false))
  }
}

async function referAPI(data) {
  const accessToken = await sessionStorage.getItem("authToken")
  const URL = `${BASE_URL}/api/v1/dashboard/refer/`
  const options = {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: `Token ${accessToken}`
    },
    method: "POST",
    data
  }
  return XHR(URL, options)
}

function* history({ data }) {
  try {
    const response = yield call(referAPI, data)
    // console.log('history success.................', response);
    yield put(referSuccess(response))
    if (response.data.referral_type == "Expert") {
      toast.success(`Refer Expert Successfully`)
    } else {
      toast.success(`Refer Business Successfully`)
    }
  } catch (e) {
    const { response } = e
    // console.log('history failure.................', response);
    yield put(referFaluire(response))
    // toast.error(`${response?.data.application_link}`)
  }
}

export default all([
  takeLatest(GET_REFER_REQUEST, getRefer),
  takeLatest(REFER_REQUEST, history)
])
